export default {
  install: function (Vue, options) {
    Vue.directive('drag', {
      inserted: (el, binding, vnode) => {
        let _this = vnode.context,
            mousemove = null,
            mouseup = null,
            dragHandle = false,
            dis = {
              x: 0,
              y: 0
            },
            dataAction = '';

        mousemove = (e) => {
          let pos = {
            x: e.pageX,
            y: e.pageY
          };

          _this.dragMove && _this.dragMove(pos, dataAction, dragHandle)
        }

        mouseup = (e) => {
          document.removeEventListener('mousemove',mousemove)
          document.removeEventListener('mouseup',mouseup)

          let pos = {
            x: e.pageX,
            y: e.pageY
          };

          dragHandle = false

          _this.dragUp && _this.dragUp(pos, dataAction, dragHandle)
        }

        el.onmousedown = (e) =>{
          e.preventDefault();
          dragHandle = true;
          dis = {
            x: e.pageX,
            y: e.pageY
          }
          dataAction = e.target.dataset.action;

          document.addEventListener('mousemove', mousemove)
          document.addEventListener('mouseup', mouseup)

          _this.dragDown && _this.dragDown(dis, dataAction, dragHandle)
        }
      }
    });
  }
};
