<template></template>

<script>
export default {
  name: 'keyboard',
  data() {
    return {
      isKeyDown: false,
      isPreventDefault: true,
    }
  },
  mounted() {
    document.addEventListener('keydown', (e) => {
      if (!this.isPreventDefault) return;
      e = e || window.event;
      let keyCode = e.keyCode || e.which || e.charCode;
      let ctrlKey = e.ctrlKey,
          metaKey = e.metaKey;
      let shiftKey = e.shiftKey;
      let altKey = e.altKey;
      let consist = '';
      if ((ctrlKey || metaKey) && !shiftKey) {
        consist = 'ctrl'
        this.preventDefaultF(e)
      } else if (shiftKey && !ctrlKey && !metaKey) {
        consist = 'shift';
        this.preventDefaultF(e)
      } else if (!ctrlKey && !metaKey && !shiftKey) {
        consist = '';
      }
      this.$bus.emit('keydown', {
        consist: consist,
        keyCode: keyCode
      })
      // 83 ctrl + s: 保存
      // 88 ctrl + x：剪切
      // 67 ctrl + c：复制
      // 86 ctrl + v：粘贴
      // 65 ctrl + a: 全选
      // 90 ctrl + z：撤销
      // 89 ctrl + y：返回
      // 77 ctrl + m：居中
      // 37 left
      // 38 up
      // 39 right
      // 40 down
      // 46 8 delete backspace
      // 32 space
      // 27 esc
    });
    document.addEventListener('keyup', function(e) {
      // if ((17 === e.keyCode) || 91 === e.keyCode || 93 === e.keyCode || e.ctrlKey) {
      //   this.isCtrlKeydown = false;
      // }
    });
  },
  methods: {
    preventDefaultF(e) {
      if (this.isPreventDefault) {
        e.preventDefault();
        return false;
      }
    }
  }
}
</script>
