<template></template>

<script>
import Undo from './undo.js';

export default {
  name: 'Undo',
  data() {
    return {
      type: '',               // 当前应该调用那个栈 值('' 'Path')
      stack: null,            // 画布栈
      editCommand: null,
      stackPath: null,        // 针对设置路径动画的撤销
      editCommandPath: null,
      canUndo: false,
      canRedo: false
    }
  },
  mounted () {
    // 创建画布栈
    this.stack = new Undo.Stack();
    // 画布栈入栈事件
    this.$bus.on("undo_redo", this.inStack);
    // 调用撤销和恢复事件
    this.$bus.on('undo_redo_vue', (data) => {
      this[data.type + this.type]()
    });
    this.editCommand = Undo.Command.extend({
      constructor: function(type, data, fn) {
        this.type = type;
        this.data = data;
        this.fn = fn;
      },
      execute: function() {},
      undo: function() {
        this.fn('_undo', this.type, this.data);
      },
      redo: function() {
        this.fn('_redo', this.type, this.data);
      }
    });
    // 路径动画入栈事件
    this.$bus.on('undo_redo_path', this.inStackPath);
    // 创建路径动画栈
    this.stackPath = new Undo.Stack();
    this.editCommandPath = Undo.Command.extend({
      constructor: function(type, data, fn) {
        this.type = type;
        this.data = data;
        this.fn = fn;
      },
      execute: function() {},
      undo: function() {
        this.fn('_undoPath', this.type, this.data);
      },
      redo: function() {
        this.fn('_redoPath', this.type, this.data);
      }
    });
    // 改变type,相当于切换栈
    this.$bus.on('change_undo_type', data => {
      this.type = data.type;
      // 是否清空路径动画栈
      if(data.clearStack) {
        this.stackPath.clearStack()
      }
      this.canUndoRedo();
    });
  },
  methods: {
    fn(mod, type, data) {
      this[mod](type, data);
      // 发送撤销和恢复的事件
      this.$bus.emit('undoRedoHandle', {
        type: type,
        mod: mod
      });
    },
    inStack(data) {
      // 画布栈入栈
      this.stack.execute(new this.editCommand(data.type, data.data, this.fn));
      this.canUndoRedo();
    },
    undo() {
      this.stack.undo();
      this.canUndoRedo();
    },
    redo() {
      this.stack.redo();
      this.canUndoRedo();
    },
    _undo(type, data) {
      
    },
    _redo(type, data) {
     
    },
  }
}
</script>
